// ÑÐ¸Ð¿ Ð¿ÑÐ¾Ð¿ÑÐ¾Ð² Ð¾Ð±ÑÑÐ½Ð¾Ð³Ð¾ Ð¸Ð½Ð¿ÑÑÐ°
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>

// Ð·Ð´ÐµÑÑ Ð¼Ñ Ð³Ð¾Ð²Ð¾ÑÐ¸Ð¼ ÑÑÐ¾ Ñ Ð½Ð°ÑÐµÐ³Ð¾ Ð¸Ð½Ð¿ÑÑÐ° Ð±ÑÐ´ÑÑ ÑÐ°ÐºÐ¸Ðµ Ð¶Ðµ Ð¿ÑÐ¾Ð¿ÑÑ ÐºÐ°Ðº Ñ Ð¾Ð±ÑÑÐ½Ð¾Ð³Ð¾ Ð¸Ð½Ð¿ÑÑÐ°
// (ÑÑÐ¾Ð± Ð½Ðµ Ð¿Ð¸ÑÐ°ÑÑ value: string, onChange: ...; Ð¾Ð½Ð¸ ÑÐ¶Ðµ Ð²ÑÐµ Ð¾Ð¿Ð¸ÑÐ°Ð½Ñ Ð² DefaultInputPropsType)
type SuperInputTextPropsType = DefaultInputPropsType & { // Ð¸ + ÐµÑÑ Ð¿ÑÐ¾Ð¿ÑÑ ÐºÐ¾ÑÐ¾ÑÑÑ Ð½ÐµÑ Ð² ÑÑÐ°Ð½Ð´Ð°ÑÑÐ½Ð¾Ð¼ Ð¸Ð½Ð¿ÑÑÐµ
    onChangeText?: (value: string) => void
    onEnter?: () => void
    error?: string
    spanClassName?: string
}

const SuperInputText: React.FC<SuperInputTextPropsType> = (
    {
        type, // Ð´Ð¾ÑÑÐ°ÑÐ¼ Ð¸ Ð¸Ð³Ð½Ð¾ÑÐ¸ÑÑÐµÐ¼ ÑÑÐ¾Ð± Ð½ÐµÐ»ÑÐ·Ñ Ð±ÑÐ»Ð¾ Ð·Ð°Ð´Ð°ÑÑ Ð´ÑÑÐ³Ð¾Ð¹ ÑÐ¸Ð¿ Ð¸Ð½Ð¿ÑÑÐ°
        onChange, onChangeText,
        onKeyPress, onEnter,
        error,
        className, spanClassName,

        ...restProps// Ð²ÑÐµ Ð¾ÑÑÐ°Ð»ÑÐ½ÑÐµ Ð¿ÑÐ¾Ð¿ÑÑ Ð¿Ð¾Ð¿Ð°Ð´ÑÑ Ð² Ð¾Ð±ÑÐµÐºÑ restProps
    }
) => {
    const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
        onChange // ÐµÑÐ»Ð¸ ÐµÑÑÑ Ð¿ÑÐ¾Ð¿Ñ onChange
        && onChange(e) // ÑÐ¾ Ð¿ÐµÑÐµÐ´Ð°ÑÑ ÐµÐ¼Ñ Ðµ (Ð¿Ð¾ÑÐºÐ¾Ð»ÑÐºÑ onChange Ð½Ðµ Ð¾Ð±ÑÐ·Ð°ÑÐµÐ»ÐµÐ½)

        onChangeText && onChangeText(e.currentTarget.value)
    }
    const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
        onKeyPress && onKeyPress(e);

        onEnter // ÐµÑÐ»Ð¸ ÐµÑÑÑ Ð¿ÑÐ¾Ð¿Ñ onEnter
        && e.key === 'Enter' // Ð¸ ÐµÑÐ»Ð¸ Ð½Ð°Ð¶Ð°ÑÐ° ÐºÐ½Ð¾Ð¿ÐºÐ° Enter
        && onEnter() // ÑÐ¾ Ð²ÑÐ·Ð²Ð°ÑÑ ÐµÐ³Ð¾
    }

    const finalSpanClassName = `${s.error} ${spanClassName ? spanClassName : ''}`
    const finalInputClassName = `${s.errorInput} ${error ? s.errorInput: s.superInput} ${className}`  // need to fix with (?:) and s.superInput

    return (
        <>
            <input
                type={'text'}
                onChange={onChangeCallback}
                onKeyPress={onKeyPressCallback}
                className={finalInputClassName}

                {...restProps} // Ð¾ÑÐ´Ð°ÑÐ¼ Ð¸Ð½Ð¿ÑÑÑ Ð¾ÑÑÐ°Ð»ÑÐ½ÑÐµ Ð¿ÑÐ¾Ð¿ÑÑ ÐµÑÐ»Ð¸ Ð¾Ð½Ð¸ ÐµÑÑÑ (value Ð½Ð°Ð¿ÑÐ¸Ð¼ÐµÑ ÑÐ°Ð¼ Ð²Ð½ÑÑÑÐ¸)
            />
            {error && <span className={finalSpanClassName}>{error}</span>}
        </>
    )
}

export default SuperInputText
